import React from 'react';
import {TabPanelContainer} from '@here/hds-tabs';

// eslint-disable-next-line no-new
new TabPanelContainer();

interface HDSTabPanelContainerProps extends React.HTMLAttributes<HTMLElement> {
  children: React.ReactNode;
  tabBarId: string;
  selectedPanel?: string;
}

const HDSTabPanelContainer = React.forwardRef((props: HDSTabPanelContainerProps, ref) => {
  const {children, selectedPanel, tabBarId, className, ...rest} = props;

  return (
    <hds-tab-panel-container
      {...(className && {class: className})}
      {...(tabBarId && {'tab-bar-id': tabBarId})}
      {...(selectedPanel && {'selected-panel': selectedPanel})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-tab-panel-container>
  );
});

HDSTabPanelContainer.defaultProps = {
  tabBarId: '',
  selectedPanel: '',
};

export default HDSTabPanelContainer;
